#box {
    border: 1px solid #ccc;
    width: 1500px;
    height: 300px;
    padding: 10px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
  }
  
  .box {
    fill: #ccc;
  }
  
  .line {
    stroke: #000;
    stroke-width: 2px;
  }
  
  .svg-container {
    width: 100%;
    height: 100%;
  }

  .paragraphe
  {
    padding-left : 30%;
    padding-right: 30%;
  }
  